import React, { Component } from 'react'
import './fullscreenbg.css';



export default class FullscreenBg extends Component {
    render() {
        return (
            <section
                className="fullscreenBg"
                id = {this.props.id}
                style={{backgroundImage: `url(${this.props.bg})`}}>
                {this.props.children}
            </section>
        )
    }
}


