import React, { Component } from 'react';

//menu
import Navbar from './components/Navbar/Navbar';
import MobileNavMenu from './components/MobileNav/MobileNavMenu';
import Backdrop from './components/Backdrop/Backdrop';

//animate-on-scroll
import "./animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';

//fullscreen wrapper
import FullscreenBg from './components/FullscreenBg/FullscreenBg';

//pages
import Home from './components/Pages/Home';
import About from './components/Pages/About';
import Process from './components/Pages/Process';
import Journey from './components/Pages/Journey';
import Products from './components/Pages/Products';
import Contact from './components/Pages/Contact';

//background images
import HomeBg from './images/Background/home.jpg';
import AboutBg from './images/Background/about.jpg';
import ProcessBg from './images/Background/process.jpg';
import JourneyBg from './images/Background/journey.jpg';
import ProductBg from './images/Background/product.jpg';
import ContactBg from './images/Background/contact.jpg';


class App extends Component {
  state = {
    mobileNavMenuOpen: false,
  };

  mobileNavToggleClickHandler = () => {
    this.setState((prevState) => {
      return {mobileNavMenuOpen: !prevState.mobileNavMenuOpen};
    });
  };

  backdropClickHandler = () => {
    this.setState({mobileNavMenuOpen: false});
  }

  render() {
    let backdrop;

    if (this.state.mobileNavMenuOpen) {
      backdrop = <Backdrop click={this.backdropClickHandler}
      />
    }
    return (
      <div className="App">
        {/* menu */}
        <Navbar
          mobileClickHandler={this.mobileNavToggleClickHandler}
          />
        <MobileNavMenu show={this.state.mobileNavMenuOpen}/>
        {backdrop}

        {/* main */}
        <FullscreenBg id='section0'bg={HomeBg}>
          <Home/>
        </FullscreenBg>

        <FullscreenBg id='section1' bg={AboutBg}>
          <About />
        </FullscreenBg>

        <FullscreenBg id='section2' bg={ProcessBg}>
            <Process/>
        </FullscreenBg>

        <FullscreenBg id='section3' bg={JourneyBg}>
          <Journey/>
        </FullscreenBg>
        <FullscreenBg id='section4' bg={ProductBg}>
          <Products />
        </FullscreenBg>
        <FullscreenBg id='section5' bg={ContactBg}>
          <Contact />
        </FullscreenBg>
      </div>
    );
  }
}

export default App;
