import React, { Component } from 'react';

import './mapmodal.css'
//map
import Map from 'pigeon-maps';
import Marker from 'pigeon-marker';
import Overlay from 'pigeon-overlay';
import { FaTimes } from 'react-icons/fa';

export default class MapModal extends Component {
    state = {
        mapOpen: false,
    };

    mapOpenClickHandler = () => {
        this.setState((prevState) => {
        return {mapOpen: true};
        });
    };

    mapCloseClickHandler = () => {
        this.setState((prevState) => {
            return {mapOpen: !prevState.mapOpen};
        });
    }

    render() {
        let mapClasses = ['map-wrapper'];
        if (this.state.mapOpen !== false) {
            mapClasses = ['map-wrapper open'];
        }

            return (
                <div className={mapClasses} >
                    <Map
                        onClick={this.mapOpenClickHandler}
                        boxClassname={'map'}
                        center={[-6.15, 107.10043117]}
                        zoom={14}
                        width= {800}
                        height= {600}
                        metaWheelZoom={true}
                        animate={true}
                        attribution={false}
                        >
                            <Marker
                                    anchor={[-6.15, 107.10043117]}
                                    payload={1}
                                    onClick={({ event, anchor, payload }) => {}}
                                    />
                            <Overlay
                                    anchor={[-6.15, 107.10043117]}
                                    offset={[15, 40]}
                                    >
                            </Overlay>
                    </Map>
                    <div className="close-map-button" onClick={this.mapCloseClickHandler}>Close<FaTimes/></div>
                </div>
            )
        }
    }
